body {
  background-color: #3FAE;
}

h1 {
  text-align: center;
  margin: 0px;
}

img{
  padding: 10px;
  margin: 0px;

}

.react-logo{
  height: 200px;
  width: 200px;
  margin: -40px;

}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.App {
  background-color: teal;
  min-height: 750px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: yellowgreen;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}
#to-do-form input::placeholder {
  color: white;
}

#to-do-form input:focus, #to-do-form button {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0px;
  background-color: black;
  cursor: pointer;
  font-weight: bolder;
  color: white;
}